.App-welcome {
  position: relative;
  background-color: #282c34;
}

.App-welcome img {
  width: 100%;
}

.App-welcome h1 {
  color: white;
  font-size: 5vw;
  text-align: left;

  position: absolute;
  top: 40%;
  left: 30%;
  transform: translate(-50%, -50%);
  text-shadow: 5px 5px #282c348e;

  opacity: 1;
  animation-name: FadeIn;
  animation-duration: 0.5s;
  transition: 0.5s;
}

.App-welcome a {
  color: #61dafb;
  opacity: 1;
  text-decoration: none;
  font-size: 2vw;
  padding: 0.5%;
  border: solid #61dafb;

  position: absolute;
  top: 63%;
  left: 17%;
  transform: translate(-50%, -50%);

  animation: FadeIn 1s, FindAHomeMoveUp 1.5s;
  transition: 1s;
}
.App-welcome a:hover {
  color: white;
  border: solid #61dafb;
  background-color: #61dafb;
}

@keyframes FindAHomeMoveUp {
  from {
    position: absolute;
    top: 65%;
    left: 17%;
  }
  to {
    position: absolute;
    top: 63%;
    left: 17%;
  }
}

@media only screen and (max-width: 600px) {
  .App-welcome h1 {
    top: 30%;
    left: 50%;
    font-size: 7vw;
  }
  .App-welcome a {
    top: 80%;
    left: 50%;
    font-size: 5.5vw;
  }

  @keyframes FindAHomeMoveUp {
    from {
      position: absolute;
      top: 90%;
      left: 50%;
    }
    to {
      position: absolute;
      top: 80%;
      left: 50%;
    }
  }
}
