.App-contactus {
  color: white;
  padding: 200px 50px;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("/public/images/Building_2.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.App-contactus a {
  color: #61dafb;
  text-decoration: none;
  border: solid #61dafb;
  padding: 10px;
  font-size: 1.5vw;
  transition: 1s;
}

.App-contactus a:hover {
  color: white;
  border: solid #61dafb;
  background-color: #61dafb;
}

.App-contactus-container {
  position: relative;
}

@media only screen and (max-width: 1000px) {
  .App-contactus a {
    font-size: 4vw;
  }
}
