.App-about-container {
  min-height: 1000px;
  position: relative;
  padding: 50px 300px;
}

.App-about {
  transition: 0.5s;
}

.App-about h2 {
  text-align: left;
}

.About-content {
  display: flex;
}

.About-content p {
  display: inline;
  text-align: left;
  padding: 10px;
}

.About-content img {
  max-width: 300px;
}

.Reverse-order p {
  order: 2;
}

@media only screen and (max-width: 1800px) {
  .App-about-container {
    padding: 50px 10%;
  }
}

@media only screen and (max-width: 810px) {
  .App-about h2 {
    text-align: center;
  }

  .About-content {
    display: unset;
    text-align: left;
  }

  .About-content p {
    display: unset;
  }

  .About-content img {
    width: auto;
  }
}
