.App-services {
  width: 100%;
  padding: 100px 0px;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(/public/images/House_2.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  min-height: 320px;
  transition: 1s;
}

.App-service-item {
  color: white;
  background-color: #282c34d0;
  margin: 20px;
  padding: 20px;
  width: 300px;
  display: inline-block;
  text-align: left;
  opacity: 1;
}

.hiden {
  display: none;
}

@media only screen and (min-width: 754px) and (max-width: 1140px) {
  .App-services {
    min-height: 700px;
  }
}

@media only screen and (max-width: 600px) {
  .App-services {
    min-height: 1000px;
  }
}
