.App-gallery {
  width: auto;
  padding: 2% 15vw;
  display: grid;
  gap: 10px;
  grid-template-columns: auto auto auto;
  min-height: auto;
  opacity: 1;
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%, 0%);
}

.App-gallery-frame {
  width: 100%;
  position: relative;
  min-height: 1100px;
}

#ResultMSG {
  font-size: 1vw;
}

@media only screen and (min-width: 1200px) and (max-width: 1400px) {
  .App-gallery-frame {
    min-height: 800px;
  }
}

@media only screen and (min-width: 810px) and (max-width: 1200px) {
  .App-gallery-frame {
    min-height: 800px;
  }
  .App-gallery {
    padding: 50px;
    align-content: center;
  }

  @media only screen and (min-width: 810px) and (max-width: 850px) {
    .App-gallery {
      width: 95%;
    }
  }
}

@media only screen and (min-width: 600px) and (max-width: 810px) {
  .App-gallery-container {
    min-height: 1500px;
  }

  .App-gallery {
    width: 95%;
    padding: 10px;
    align-content: center;
    display: grid;
    gap: 10px;
    grid-template-columns: auto auto;
  }
}

@media only screen and (max-width: 600px) {
  .App-gallery {
    width: 96%;
    padding: 10px;
    display: grid;
    gap: 4px;
    grid-template-columns: auto;
    align-content: center;
    position: unset;
    transform: translate(0%, 0%);
  }
}
