* {
  scroll-behavior: smooth;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.fadeIn {
  animation: FadeIn 0.5s;
}

.moveUp {
  animation: MoveUp 1s;
}

.shake {
  animation: Shake 1s;
}

.box {
  width: 100px;
  height: 100px;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}

@media only screen and (max-width: 600px) {
  .box {
    top: 0%;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes FadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes MoveUp {
  from {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%, 0%);
  }
  to {
    position: absolute;
    top: 0%;
    left: 50%;
    transform: translate(-50%, 0%);
  }
}

@keyframes Shake {
  0% {
    rotate: 10deg;
  }
  20% {
    rotate: -10deg;
  }
  40% {
    rotate: 10deg;
  }
  60% {
    rotate: -10deg;
  }
  80% {
    rotate: 0deg;
  }
  100% {
    rotate: 0deg;
  }
}
